.all-video {
	display: flex;
	flex-wrap: wrap;

	// .all-video__item

	&__item {
		width: calc(33% - 16px);
		// margin-right: 20px;
		display: flex;
		flex-direction: column;
		margin-bottom: 39px;
		height: 100%;

		&:hover {
			.all-video__link {
				color: $base;
				cursor: pointer;
			}
		}

		&:nth-child(3n-1) {
			margin: 0 28px 39px;
		}
	}

	// iframe,
	// img {
	// 	max-height: 220px;
	// 	margin-bottom: 0px;
	// 	height: 220px;
	// 	pointer-events: none;
	// }

	iframe,
	img {
		max-height: 170px;
		margin-bottom: 0px;
		height: 170px;
		pointer-events: none;
	}

	a[data-type="iframe"] {
		display: flex;
		flex-direction: column;
		position: relative;
		overflow: hidden;
		text-decoration: none !important;

		picture {
			position: relative;

			&:after {
				content: '';
				// content: url('../images/play btn.svg');
				// background-image: url('../images/play btn.svg');
				background-image: url('../images/play.svg');
				position: absolute;
				background-repeat: no-repeat;
				background-size: 40px 40px;
				background-color: rgba(0, 0, 0, 0.5);
				background-position: center 65px;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
			}
		}


		&:hover {
			img {
				transform: scale(1.1);
			}
		}

		img+iframe~:after {
			display: none;
		}
	}


	// .all-video__link

	&__link {
		color: #333333;
		font-family: "Roboto Condensed", sans-serif;
		font-size: 20px;
		font-weight: 400;
		line-height: 24px;
		background: white;
		padding-top: 7px;
		z-index: 1;
		text-decoration: none !important;
	}

	// .all-video__bottom

	&__bottom {
		display: flex;
		flex-direction: column;
		align-items: center;


		.load-more {
			margin-top: 31px;
			border-radius: 3px;
			border: 1px solid #f1f1f1;
			color: $base;
			font-family: "Roboto Condensed", sans-serif;
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			padding: 5px 45px;
			width: fit-content;
			margin-bottom: 11px;
			text-decoration: none !important;
		}
	}

	@media screen and (max-width: 767px) {
		flex-direction: column;
		flex-wrap: nowrap;

		&__item {
			width: 100%;
			margin-bottom: 26px;

			&:nth-child(3n-1) {
				margin: 0 0px 26px;
			}
		}

		.all-video__link {
			color: #333333;
			font-family: "Roboto Condensed", sans-serif;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
		}

		a[data-type="iframe"] {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		iframe {
			margin-bottom: 13px;
		}

		&__bottom {
			margin-bottom: 7px;

			.load-more {
				// margin-bottom: 0px;
			}
		}


	}
}
