.category {
	font-family: "Roboto Condensed", sans-serif;
	width: 100%;
	max-width: 100%;
	position: relative;
	// margin-bottom: 32px;

	* {
		box-sizing: border-box;
	}

	// .category__items

	.category__items {
		display: flex;
		flex-wrap: wrap;
	}

	// .category__item

	.category__item {
		width: calc((100%/3)-20px);
		display: flex;
		flex-direction: column;
		background-repeat: no-repeat;

		background-position: calc(100%-3px) 10px;
		padding: 22px 173px 16px 26px;
		background-color: #f8f8f8;
		height: 156px;
		text-decoration: none !important;

		&:nth-child(-n+3) {
			margin-bottom: 28px;
		}

		&:nth-child(3n+2) {
			margin-left: 29px;
			margin-right: 29px;
		}

		&:last-child {
			padding: 22px 163px 16px 26px;
		}

		&:nth-child(1) {
			// background-size: 39%;
			// background-position: calc(100% + 2px) 10px;
		}

		&:nth-child(2) {
			// background-size: 37%;
		}

		&:nth-child(3) {
			// background-size: 49%;
			background-position: calc(100%-3px) 2px;
		}

		&:nth-child(4) {
			// background-size: 30%;
			// background-position: calc(100% - 20px) 10px;
		}

		&:nth-child(5) {
			// background-size: 37%;
			background-position: calc(100% - 0px) 1px;
		}

		// &:nth-child(6) {
		// 	background-size: 38%;
		// 	background-position: calc(100% - 9px) 14px;
		// }



		&:hover {
			// background-color: $base;

			.category__caption {
				// color: white;
				text-decoration: underline !important;
			}

			.category__price {
				// color: #ffffff;
				// margin-top: auto;
			}
		}

		* {
			text-decoration: none !important;
		}
	}

	// .category__caption

	&__caption {
		color: $base;
		font-size: 18px;
		font-weight: 400;
		line-height: 26px;
		margin-bottom: 23px;
	}

	// .category__price

	&__price {
		color: #333333;
		font-size: 16px;
		font-weight: 700;
		line-height: 24px;
		margin-top: auto;
		white-space: nowrap;

		br {
			display: none;
		}
	}


	.swiper-pagination {
		display: none;
	}

	&+.special-offers {
		margin-top: 56px;
	}

	@media screen and (max-width: 1024px) {
		.category__item:nth-child(3n+2) {
			margin-left: 16px;
			margin-right: 16px;
			flex-grow: 1;
		}

		.category__price {
			font-size: 14px;
		}
	}

	@media screen and (max-width: 767px) {
		padding-bottom: 0px;

		.category__items {
			flex-wrap: nowrap;
		}

		.swiper-pagination {
			display: flex;
			transform: translateX(-50%);
			left: 50%;
			bottom: 0;

			&-bullet {
				margin: 5px 3px !important;
				background-color: #d8d8d8;
				opacity: 1;

				&-active {
					background-color: $base;
				}
			}

		}

		.category__item {
			// background-position: calc(100%) top;
			background-position: calc(100% + 30px) center !important;
			background-size: contain !important;
			padding: 23px 99px 19px 16px !important;
			height: auto;
			justify-content: space-between;
			max-width: 100%;

			&:nth-child(-n+3) {
				margin-bottom: 0px;
			}

			&:nth-child(3n+2) {
				margin-left: auto;
				margin-right: auto;
			}

			// &:hover {
			// 	background-color: $base;

			// 	.category__caption {
			// 		color: #ffffff;
			// 		text-decoration: underline;
			// 	}

			// 	.category__price {
			// 		color: #ffffff;
			// 	}
			// }
		}

		.category__caption {
			// color: #bb0000;
			font-family: "Roboto Condensed", sans-serif;
			font-size: 16px;
			font-weight: 400;
			line-height: 26px;
			margin-bottom: 18px;
		}

		.category__price {
			font-size: 14px;
			font-weight: 700;
			line-height: 1.3;
			white-space: normal;

			br {
				display: block;
			}
		}

		.swiper-pagination {
			position: static;
			transform: none;
			justify-content: center;
		}

		.category-container {
			margin-bottom: 8px;
		}

		&+.special-offers {
			margin-top: 26px;
		}
	}
}
