@font-face {
	font-family: "Webnar - Black";
	src: url(fonts/webnar/Webnar_Black.otf);
	font-weight: 400;
	font-display: swap;
}

@font-face {
	font-family: "Arial";
	src: url("fonts/arial/ArialMT.eot");
	src: local("__"), url("../../fonts/arial/ArialMT.woff") format("woff"), url("fonts/arial/ArialMT.svg") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "Roboto Condensed", sans-serif;
	src: local('Roboto Condensed'), local('RobotoCondensed-Regular'), url('/templates/main/build/fonts/robotocondensed/Robotocondensed.woff2') url('/templates/main/build/fonts/robotocondensed/Robotocondensed.woff2') format('woff2'), url('/templates/main/build/fonts/robotocondensed/Robotocondensed.woff') format('woff'), url('/templates/main/build/fonts/robotocondensed/Robotocondensed.ttf') format('truetype');
	font-weight: 400;
	font-style: normal;
}
