.gallery {
	max-width: 1436px;
	margin-left: auto;
	width: 100%;
	margin-right: auto;
	position: relative;
	background-color: #f1f1f1;
	margin-bottom: 33px;

	img {
		width: auto;
		height: 100%;
		max-width: 100%;
	}

	.swiper-container-main {
		position: relative;
		overflow: hidden;
		height: 366px;

	}

	&__items {
		display: flex;
		max-width: 1240px;
		align-items: flex-start;
		margin-left: auto;
		margin-right: auto;
		padding: 26px 33px;
		width: 100%;
	}


	&__item {
		width: 319px;
		height: 642px;
		flex-shrink: 0;
		margin-top: -320px;
		z-index: 89;
		margin-left: -8px;
		margin-bottom: -95px;
		position: relative;
		display: flex;
		position: relative;
		list-style: none;
		z-index: 89;

		.swiper-wrapper {
			height: 100%;
			width: 100%;
			// transform: none !important;

			.swiper-slide {
				opacity: 0;
			}

			.swiper-slide-active {
				opacity: 1;
			}

			// .swiper-slide-next,.swiper-slide-prev{
			// 	position: absolute;
			// 	top: 0;
			// 	left: 0;
			// }
		}

		.button__open {
			width: 34px;
			height: 34px;
			border-radius: 3px;
			background-color: $base;
			color: #ffffff;
			background: url("../images/holod-button.png") no-repeat;
			position: absolute;
			top: 5%;
			left: 5%;
			z-index: 9999;
			overflow: hidden;
			cursor: pointer;


			&:after {
				content: "";
				position: absolute;
				border-radius: 3px;
				top: 0;
				left: -18px;
				width: 205%;
				height: 100%;
				background: linear-gradient(to left, rgba(255, 255, 255, .0), rgba(255, 255, 255, .8), rgba(255, 255, 255, .0)) no-repeat -2em 0%;
				background-size: 2em 100%;
				// -moz-transform: skewX(-50deg);
				// -o-transform: skewX(-50deg);
				// -webkit-transform: skewX(-50deg);
				transform: skewX(-50deg);
			}

			&.blink_on:after {
				transition: 1.5s linear;
				background-position: 60px 0%;

			}

			&.active {
				transform: rotate(180deg);

				&:after {
					transform: skewX(-50deg) rotate(-180deg);
				}

			}
		}

		.swiper-slide {
			padding-left: 20px;
		}
	}

	&__point {
		position: absolute;
		width: 22px;
		height: 22px;
		background: url("../images/point-small.png") 0/22px no-repeat;
		cursor: pointer;

		&:hover {
			background: url("../images/point-big.png") 0/22px no-repeat;

			.gallery__description {
				display: block;
			}
		}
	}

	&__description {
		display: none;
		position: absolute;
		width: 500px;
		left: 40px;
		top: 50%;
		transform: translateY(-50%);
		border-radius: 2px;
		box-shadow: 0 2px 12px 0 rgba(82, 64, 64, 0.17);
		background-color: #ffffff;
		padding: 8px 11px;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: condensed;
		line-height: normal;
		letter-spacing: normal;
		color: #212121;
		z-index: 99999;
	}

	&__tablet {
		display: flex;
		align-items: flex-start;
		width: 100%;
	}

	&__el {
		margin-right: auto;
		display: flex;
		background-color: #ffffff;
		height: auto;
		flex-basis: calc(25% - 21.5px);

		.gallery__parent {
			display: flex;
			flex-direction: column;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	&__link {
		display: flex;
		flex-direction: column;
		width: 100%;

		img {
			width: 100%;
			max-width: 100%;
			height: auto;
			max-height: 100%;
		}

		&:hover {
			text-decoration: none !important;

			.gallery__article {
				color: $base;
				text-decoration: underline;
			}
		}
	}

	&__article {
		font-size: 22px;
		font-weight: normal;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.27;
		letter-spacing: normal;
		color: #212121;
		padding: 14px 12px 9px 24px;
	}

	&__text {
		font-family: "Arial";
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.36;
		letter-spacing: normal;
		color: #666666;
		padding: 0 24px 10px;
		text-decoration: none !important;
	}

	.swiper-scrollbar {
		display: none;
	}

	.swiper {
		&-slide {
			padding-left: 0px;
		}



		&__item {
			position: absolute;
			align-items: flex-start;
			top: 0;
			left: 0;
			width: 100%;
			max-width: 100%;
			height: 100%;
			max-height: 100%;
			display: flex;
			flex-wrap: wrap;
			padding-top: 44px;
			// background: #0000006b
		}



		&__title {
			width: 550px;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			margin-left: 130px;
			margin-top: 0;
			border-radius: 3px;
			background-color: #212c89e3;
			// padding-left: 52px;
			// padding-top: 24px;
			// padding-bottom: 32px;
			padding: 70px 52px 16px;
			height: 274px;

			.span {

				&__small {
					color: #ffffff;
					font-family: "Roboto Condensed", sans-serif;
					font-size: 30px;
					font-weight: 400;
					line-height: 40px;
					margin-bottom: 16px;

					a {
						color: #ffffff;
						text-decoration: none;
					}
				}

				&__big {
					color: #ffffff;
					font-family: "Roboto Condensed", sans-serif;
					font-size: 16px;
					font-weight: 400;
					line-height: 24px;
					margin-bottom: 13px;
					width: 100%;
				}
			}
		}

		&__box {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-top: -80px;
		}

		&__link {
			width: auto;
			height: 34px;
			align-self: flex-start;
			display: flex;
			justify-content: center;
			padding: 8px 16px;
			border-radius: 2px;
			border: solid 1px #ffffff;
			max-width: 172px;
			font-size: 14px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: normal;
			text-align: left;
			color: #ffffff;
			text-transform: uppercase;
			text-decoration: none !important;
		}

		&-pagination {
			width: auto !important;
			transform: translateX(-50%);
			left: 50% !important;
			border-radius: 8px;
			background-color: rgba(102, 102, 102, 0.6);
			display: flex;
			padding: 0 6px;

			&-bullet {
				opacity: 0.5;
				margin: 5px 3px !important;
				background: #ffffff !important;
			}
		}

		&-pagination-bullet-active {
			// background: $base !important;
			box-sizing: border-box;
			opacity: 1;
		}

		&-button-main {
			opacity: 0;
		}

	}

}


@media screen and (max-width: 900px) {}

@media screen and (max-width: 767px) {
	.gallery {
		margin-bottom: 28px;
		max-width: 100%;

		.swiper-container-main {
			height: auto;
		}

		.swiper-slide {
			flex-direction: column;
			align-content: baseline;
			justify-content: flex-start;
			overflow: hidden;
			display: flex;

			// display: none;

			// &-active,&-next {
			// 	display: flex;
			// }

			img {

				max-width: unset;
				height: 235px;
				display: block;
				position: absolute;
				top: 0;
				// right: 0px;
				right: -144px;
			}

		}

		.swiper__item {
			position: static;
			height: auto;
			background-color: $base;
			padding-top: 0;
			flex-grow: 1;
			align-items: center;
			flex-direction: column;
			display: flex;
			padding-top: 225px;
			justify-content: center;

			.gallery__parent {
				min-width: 212px;
			}

		}

		.swiper__title {
			padding: 34px 16px 16px;
			flex-grow: 1;
			width: 100% !important;
			margin-left: 0;
			background-color: $base;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			max-width: unset;
			height: auto;

			.span__small {
				margin-bottom: 8px;
				color: #ffffff;
				font-family: "Roboto Condensed", sans-serif;
				font-size: 30px;
				font-weight: 400;
				line-height: 34px;
				text-align: center;

			}

			.span__big {
				margin-bottom: 13px;
				color: #ffffff;
				font-family: Arial, sans-serif;
				font-size: 14px;
				font-weight: 400;
				line-height: 20px;
				text-align: center;
			}

			.swiper__link {
				margin: auto auto 0;
			}

			p {
				margin-bottom: 0;
			}
		}

		.swiper-wrapper {
			// align-items: baseline;
		}

		.swiper-pagination {
			top: 205px;
			bottom: unset;
		}



		&__items {
			padding: 16px;
			// padding: 16px 0;
			max-width: 100%;
			margin: 0;
			position: relative;
		}

		&__item {
			// display: none;
		}

		&__article {
			color: #212121;
			font-family: "Roboto Condensed", sans-serif;
			font-size: 16px;
			font-weight: 400;
			line-height: 22px;
			padding: 6px 12px 4px;
		}

		&__text {
			color: #666666;
			font-family: Arial, sans-serif;
			font-size: 14px;
			line-height: 19px;
			padding: 0px 12px 5px 12px;
		}


		&__tablet {
			display: flex;
			flex-direction: column;
			flex-grow: 1;
			// padding-left: 10%;
		}

		&__el {
			height: unset;
			margin-left: unset;
			width: 100%;
			margin-bottom: 7px;
			flex-basis: unset;

			&:last-child {
				margin-bottom: 0;
			}

			.gallery__text {
				padding: 0px 12px 5px 12px;
			}
		}

		.gallery__parent {
			z-index: 2;
			margin-left: 21%;
			background: white;
		}

		&__link {
			flex-direction: row;
			margin: 0 auto;
			align-items: center;
			position: relative;

			picture {
				width: 21%;
				height: auto;
				height: 100%;
				position: relative;
				top: 0;
				overflow: hidden;
				padding-top: 12%;
			}

			img {
				height: auto;
				max-width: unset;
				min-height: 56px;
				position: absolute;
				top: 0;
				left: 0;
				// right: 0;
				width: calc(21% + 50px);
				object-fit: cover;
			}
		}


		&+.special-offers {
			margin-top: 0px;
		}

		&+.js-ecom_product-list[data-list="Main Result"] {

			.special-offers {
				margin-top: 0px;
			}
		}
	}


	// .fixed {
	// 	.header__icons {
	// 		position: absolute;
	// 		top: 18px;
	// 		right: 25px;
	// 	}
	// }
}
