.articles {
	padding: 0 0 88px;

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		padding-top: 24px;
	}

	.navchain {
		margin-bottom: 0;
	}

	&__title {
		font-size: 32px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		letter-spacing: normal;
		text-align: left;
		color: #212121;
		margin-bottom: 35px;
		line-height: 38px;
	}

	&__link {
		margin-left: auto;
		padding: 10px 12px;
		border-radius: 2px;
		border: solid 1px $base2;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: left;
		color: $base2;
		text-transform: uppercase;
		text-decoration: none !important;

		// &:hover {
		// 	color: #fff;
		// 	// background: #394791 !important;
		// }
	}

	&__items {
		display: flex;

		&.swiper-wrapper {
			.swiper-slide {
				padding-left: 0;
				margin-right: 0;
			}
		}
	}

	&__item {
		width: calc(25% - 20px);
		box-sizing: border-box;
		margin-right: 28px;
		display: flex;
		// flex-wrap: wrap;
		flex-direction: column;
		align-items: baseline;
		align-content: flex-start;
		align-items: baseline;
		align-content: flex-start;
		position: relative;


		&:last-child {
			margin-right: 0;
		}

		&:hover {
			// max-height: unset;
			// height: 100%;

			.articles__info {
				height: 80px;
				overflow: hidden;

				&:after {
					display: none;
				}
			}

			// .articles__pic:after {
			// 	display: block;
			// }

		}
	}

	&__pic {
		width: 100%;
		height: 170px;
		display: flex;
		order: -5;
		margin-bottom: 1rem;

		img {
			max-width: 100%;
			height: auto;
			max-height: 100%;
			width: 100%;
		}

		&:after {
			color: #2e4d8a;
			font-size: 14px;
			font-weight: 700;
			line-height: 17px;
			text-decoration: underline;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			content: ('Читать далее');
			display: none;
		}
	}

	&__img {
		// margin-bottom: 16px;
	}

	&__text {
		width: 100%;
		font-size: 20px;
		font-weight: 400;
		line-height: 23px;
		margin-bottom: 9px;
		color: #333333;
		flex: 1 0 auto;

		&:hover {
			color: $base;
		}

	}

	&__date {
		font-family: "Arial";
		font-size: 12px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: left;
		color: #666666;
		margin-bottom: 7px;
		order: -1;
	}

	&__info {
		color: #666666;
		font-family: "Arial";
		font-size: 14px;
		line-height: 20px;
		position: relative;
		height: 80px;
		overflow: hidden;
		margin-bottom: 22px;
		cursor: context-menu;

		// &:after {
		// 	content: '';
		// 	position: absolute;
		// 	height: 50px;
		// 	width: 100%;
		// 	bottom: 0;
		// 	left: 0;

		// 	background: linear-gradient(to top, white, rgba(255, 255, 255, 0.3));
		// }
	}




	@media screen and (max-width: 767px) {
		padding-bottom: 31px;

		&__items {
			flex-wrap: wrap;
		}

		&__item {
			width: 240px;
			flex-grow: 1;
			margin-right: 0px;
			margin-bottom: 14px !important;
			// flex-grow: 1;
			flex-direction: column;
			flex-wrap: nowrap;

			// &:last-child {
			// 	display: none;
			// }
			&:nth-child(odd) {
				margin-right: 15px;
			}


		}

		&__text {
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			width: inherit;
		}

		.container {
			padding-top: 26px !important;
		}

		&__info {
			display: none;
		}

		&__link {
			margin-top: 0;
			width: 100%;
			text-align: center;
			order: 3;

		}

		&__title {
			font-size: 24px;
			font-weight: 400;
			margin-bottom: 23px;
		}

		&__pic {
			max-height: 200px;
			margin-bottom: 20px;
			flex-direction: column;
			width: 100%;
			height: auto;

			img {
				height: auto;
				width: 100%;
				// align-self: center;
			}

			&:after {
				display: none !important;
			}

		}
	}

	@media screen and (max-width: 525px) {

		.articles__items &__item {
			width: 100%;
			margin-right: 0px;
		}

	}


}

.seo-text.text-default+.articles {
	.articles__item {
		width: calc(25% - 20px);
		box-sizing: border-box;
		margin-right: 28px;
		display: flex;
		// flex-wrap: wrap;
		flex-direction: column;
		align-items: baseline;
		align-content: flex-start;
		align-items: baseline;
		align-content: flex-start;
		position: relative;
		max-height: 388px;

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			max-height: unset;
			// height: 100%;

			.articles__info {
				height: 80px;
				overflow: hidden;

				&:after {
					display: none;
				}
			}

			.articles__pic:after {
				display: block;
			}

		}
	}

	.articles__pic {
		width: 100%;
		height: 170px;
		display: flex;
		order: -5;
		margin-bottom: 1rem;

		img {
			max-width: 100%;
			height: auto;
			max-height: 100%;
			width: 100%;
		}

		&:after {
			color: #2e4d8a;
			font-size: 14px;
			font-weight: 700;
			line-height: 17px;
			text-decoration: underline;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			content: ('Читать далее');
			display: none;
		}
	}
}

