.header {
	display: flex;
	justify-content: space-between;
	margin-left: auto;
	width: 100%;
	margin-right: auto;
	position: relative;

	.header-inner & {
		flex-direction: row-reverse;
	}

	&__blocks {
		width: 100%;
		z-index: 999;
		box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.09);
	}

	&__block {

		position: relative;
		display: flex;
		z-index: 3;

		.container {
			display: flex;
			position: relative;
			align-items: center;
			padding: 0 0 0 2px;
		}

		&_top {
			justify-content: center;
			align-items: center;
			background: #ffffff;
			height: 39px;
			z-index: 9;
		}

		&_bottom {
			height: 74px;
			color: white;
			flex-wrap: wrap;
			position: relative;
			// margin: 2px 0;
			border-top: 1px solid #f1f1f1;
		}

		.hamburger {
			display: none;
		}

	}

	&__link {
		font-family: 'fonta27c5975a1a88dcd17c7c606902044d9';
		font-size: 13px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #666666;
	}

	&__img {
		z-index: 100;
		width: 56px;
		height: 58px;
		margin-top: -30px;
		margin-right: 16px;
		min-width: 56px;
	}

	&__telephones {
		// height: 28px;
		display: flex;
		align-items: center;
		margin-left: auto;
		// flex-grow: 1;
		// margin-bottom: 3px;
		margin-left: auto;

		.telephones {
			display: flex;
			flex-direction: column;
			margin-left: auto;
		}
	}

	&__phone {
		font-family: 'Roboto Condensed', sans-serif;
		font-size: 18px;
		font-weight: normal;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.44;
		letter-spacing: normal;
		color: #333333;
		white-space: nowrap;
		text-decoration: none !important;
	}

	&__call-phone-button {
		width: 142px;
		padding: 5px 15px;
		font-family: 'Roboto Condensed', sans-serif;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.71;
		letter-spacing: normal;
		color: $base2;
		border-radius: 3px;
		background-color: #ffffff;
		border: solid 1px $base2;
		display: flex;
		align-items: center;
		justify-content: center;
		text-transform: uppercase;
		display: flex;
		white-space: nowrap;
		margin-right: 43px;

		&:hover {
			text-decoration: unset;
		}
	}

	&__icons {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 24px;
		margin-left: 29px;
		margin-right: 9px;
		align-self: flex-end;
	}

	.search__popup {
		position: absolute;
		width: 100.5%;
		top: 96%;
		background-color: #ffffff;
		padding: 30px 0 26px;
		display: flex;
		justify-content: center;
		z-index: 1001;
		right: 100%;
		opacity: 0;

		&-open {
			display: flex;
			right: -0.3%;
			opacity: 1;
		}
	}

	&__overlay {
		display: none;
		left: 0;
		top: 115px;
		position: fixed;
		width: 100%;
		height: 100vh;
		opacity: 0.6;
		background-color: #000000;
		z-index: 1000;

		&-open {
			display: flex;
		}
	}

	&__form {
		position: relative;
		width: 45%;
		display: flex;
		justify-content: space-between;

		input {
			border: 0;
		}
	}

	&__button {
		max-width: 24px;
		width: 100%;
		height: 24px;
		background: url("https://icongr.am/clarity/close.svg?color=666666") 0/24px no-repeat;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 5%;
		display: none;
	}

	&__input {
		width: 100%;
		background: inherit;
		border-bottom: 1px solid #666666 !important;
		font-size: 24px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		color: #333333;
		padding-bottom: 15px;
	}

	&__submit {
		background: url("../images/search.svg") 0/20px no-repeat;
		border: 0;
		position: absolute;
		right: 0;
		display: none;
	}

	&__search {
		width: 24px;
		height: 100%;
		display: flex;
		margin-right: 35px;
		cursor: pointer;

		&:before {
			content: '';
			width: 20px;
			height: 100%;
			flex-shrink: 0;
			margin-right: 4px;
			background-size: 20px;
			background-position: center center !important;
			background: url("../images/search.svg") no-repeat;
		}

		&-open,
		&:hover {
			&:before {
				background: url("../images/musica-searcher-1.svg") no-repeat;
			}

		}



	}

	&__favorite,
	&__compare,
	&__basket {
		min-width: 39px;
		height: 100%;
		display: flex;
		justify-content: center;
		text-decoration: none !important;



		&:before {
			content: '';
			width: 20px;
			height: 100%;
			flex-shrink: 0;
			background-size: 20px !important;
			background-position: center center !important;
		}

		&.active {}
	}

	&__favorite {
		&:before {
			background: url("../images/header-fav.svg") no-repeat;
		}

		margin-right: 36px;

		&:hover {

			&:before {
				background: url("../images/header-fav-active.svg") no-repeat;
			}
		}

		&.active {
			position: relative;

			&:before {
				background: url('../images/header-fav-active.svg') no-repeat;
			}
		}
	}

	&__compare {
		&:before {
			background: url("../images/header-compare.svg") no-repeat;
		}

		margin-right: 36px;

		&:hover {

			&:before {
				background: url("../images/header-compare-active.svg") no-repeat;
			}
		}

		&.active {
			position: relative;

			&:before {
				background: url('../images/header-compare-active.svg') no-repeat;
			}
		}
	}

	&__basket {
		&:before {
			background: url("../images/shopping-cart-head.svg") no-repeat;
		}

		margin-right: 5px;

		// &-open,
		&:hover {

			&:before {
				background: url("../images/shopping-cart__color.svg") no-repeat;
			}
		}

		&.active {
			position: relative;

			&:before {
				background: url('../images/shopping-cart__color.svg') no-repeat;
			}
		}
	}

	&__number {
		font-family: Arial, sans-serif;
		font-size: 13px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #666666;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 100%;
		text-decoration: none !important;
		display: none;
		position: absolute;
		top: -8px;
		right: -3px;

		.active & {
			color: rgb(242, 44, 22) !important;
			display: flex;
		}
	}

	.hamburger {
		appearance: none;
		-moz-appearance: none;
		-webkit-appearance: none;
		outline: 0;
		border: 0;
		background: none;
		padding: 0;
		order: -2;

		&__img {
			width: 24px;
			height: 24px;
			background: url('../images/rectangle-6.svg')
		}
	}

	&__mobile-tel {
		display: none;
	}

	// .time-work {
	// 	display: none;
	// }

	@media screen and (max-width: 767px) {
		z-index: 99999;

		.header {
			&__blocks * {
				transition: 0s;
			}


			&__img {
				display: none;
			}

			&__number {
				position: absolute;
				/* Style for "Oval" */
				width: 13px;
				height: 13px;
				border: 1px solid #ffffff;
				background-color: #e0412b;
				color: #ffffff !important;
				font-family: Arial, sans-serif;
				font-size: 8px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				top: -6px;
				right: -5px;
			}


			&__block_bottom {
				display: none;

				&.active {
					display: block;
					transform: scale(1);
					position: fixed;
					top: 57px;
					left: 0;
					width: 100%;
					height: 100%;
					transition: transform 0.3s;
					display: flex;
					align-items: baseline;
					box-shadow: 0 2px 25px 0 rgba(0, 0, 0, 0.14);
					background-color: #ffffff;
					overflow: auto;
					padding-bottom: 12vh;
					// padding-top: 52px;

					&.padding {
						padding-top: 52px;


					}
				}
			}

			&__block_top {
				height: 60px;
				border-bottom: 1px solid #f1f1f1;
				align-items: flex-start;
				padding-top: 16px;


				.header__icons {
					display: flex;
					// flex-grow: 1;
					max-width: 225px;
				}

				.menu__list {
					display: flex;
				}

				&.active {
					// height: 112px;

					// .header__favorite,
					// .header__compare {
					// 	display: flex;
					// }
				}

				&:after {
					content: '';
					height: 1px;
					width: 100%;
					border-bottom: 1px solid #f1f1f1;
					position: absolute;
					top: 60px;
				}

				&.padding {
					height: 112px;

					.header__compare,
					.header__favorite {
						display: flex;
					}

				}
			}

			&__icons {
				flex: unset;
				flex-basis: unset;
				margin-left: auto;
				justify-content: unset;
				min-width: 116px;
				align-self: unset;
				margin-right: 0px;
				flex-grow: 0;
			}

			&__favorite,
			&__compare,
			&__basket {
				min-width: 20px;
				width: 20px;
				height: 20px;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				position: relative;

				&.active .header__number {
					color: white !important;
				}
			}

			&__basket {
				&:before {
					background-size: 22px !important;
					margin-right: 0;
				}

				&:hover {

					&:before {
						background-size: 22px !important;
					}
				}
			}

			&__favorite {
				display: none;
				position: absolute;
				margin-right: 12px;
				top: calc(100% - 35px);
				left: 18px;
				align-items: center;
				width: auto;
				height: 24px;


				&:before {
					background-size: 23px !important;
					margin-right: 0;
					// background: url(../images/Favorite-yandex.svg) no-repeat;
				}

				&:after {
					content: 'Отложенное';
					color: #212c89;
					font-family: Arial, sans-serif;
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					margin-left: 10px;
				}

				&:hover {

					// &:before {
					// 	background-size: 22px !important;
					// }
				}

				.header__number {
					top: -3px;
					left: 13px;
				}

			}

			&__compare {
				display: none;
				position: absolute;
				margin-right: 12px;
				top: calc(100% - 35px);
				right: 28px;
				align-items: center;
				width: auto;
				height: 24px;


				&:before {
					background-size: 23px !important;
					margin-right: 0;
					// background: url(../images/compare-ya.svg) no-repeat;
				}

				&:after {
					content: 'Сравнение';
					color: #212c89;
					font-family: Arial, sans-serif;
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					margin-left: 10px;
				}

				&:hover {

					// &:before {
					// 	background-size: 22px !important;
					// }
				}

				.header__number {
					top: -3px;
					left: 13px;
				}
			}


			&__search {
				margin-right: 0px;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				order: -1;

				&:before {
					background-size: 18px;
					margin-right: 0;
					width: 18px;
				}

				&:hover {

					&:before {
						// background-size: 19px !important;
					}
				}
			}

			// &__favorite {
			// 	// width: 26px;

			// 	// .header__number {
			// 	// 	position: absolute;
			// 	// 	/* Style for "Oval" */
			// 	// 	width: 11px;
			// 	// 	height: 11px;
			// 	// 	border: 1px solid #ffffff;
			// 	// 	background-color: #d8d8d8;
			// 	// 	color: #666666;
			// 	// 	font-family: Arial,sans-serif;
			// 	// 	font-size: 8px;
			// 	// 	border-radius: 50%;
			// 	// 	display: flex;
			// 	// 	align-items: center;
			// 	// 	justify-content: center;
			// 	// 	top: 0;
			// 	// 	right: 6px;
			// 	// }
			// }

			// &__compare {
			// 	width: auto;
			// 	max-width: 40px;
			// 	margin-right: 11px;
			// }

			&__mobile-tel {
				width: 18px;
				height: 100%;
				display: flex;
				background: url('../images/Fill_1.svg') no-repeat;
				background-position: center center;
				margin-right: 0px;
				order: -1;
				background-size: 18px !important;
				align-items: center;
				margin: 0 24px;
			}

			&__basket {
				width: auto;
				max-width: 40px;
				margin-right: -0px;

			}

			// &__number {
			// 	font-size: 13px;
			// 	font-weight: normal;
			// 	font-style: normal;
			// 	font-stretch: normal;
			// 	line-height: 1;
			// 	letter-spacing: normal;
			// 	color: #666666;
			// 	padding-top: 4px;
			// }



			&__form {
				width: 95%;
			}

			&__telephones {
				margin-left: 0;
				flex-direction: column;
				height: unset;
				align-items: baseline;
				margin-top: 16px;
				width: 100%;

				.telephones {
					order: -1;
					margin-left: 0;
					flex-direction: column;
					width: 48%;

					.header__phone:last-child {
						// margin-left: 1rem;
					}
				}

				.header__call-phone-button {
					color: $base;
					font-family: Arial, sans-serif;
					font-size: 10px;
					line-height: 22px;
					display: flex;
					justify-content: flex-end;
					align-items: center;
					margin: 5px 0 0 0;
					padding-left: 0;
					border: 0;
					width: auto;
					text-decoration: none !important;
					flex-wrap: wrap;
					display: flex;

					&::before {
						content: '';
						background: url('../images/footer__call.png');
						// background: url('../images/social-items-vk.png') no-repeat #f8f8f8;
						margin-right: 10px;
						width: 12px;
						height: 12px;
					}

					&::after {
						content: '';
						border-bottom: 1px dashed $base;
						width: calc(100% - 24px);
						height: 0px;
						margin-top: -5px;
					}

				}
			}
		}

		.container {
			justify-content: unset;
			position: unset;
			flex-wrap: wrap;
		}

		.hamburger {
			display: block;
			margin-right: 16px;
			margin-top: 0px;

			&__img {
				width: 18px;
				height: 18px;
				background: url('../images/rectangle-6.svg') 0/18px;

			}
		}

		.opened {
			.hamburger__img {
				background: url('https://icongr.am/entypo/cross.svg?color=7d8082') 0/18px center;
				width: 18px;
				height: 18px;
			}
		}
	}
}
